.board {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.item {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(0px, 0px);
    cursor: move;
}

.item img {
    width: 100%;
    height: 100%;
    pointer-events: none;
}